<template>
    <el-dialog class="login_dialog" title="" :visible.sync="loginFormVisiable" @close="resetLoginForm" width="400px" center>
        <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="60px" class="login_form">
            <!--        -->
            <el-form-item prop="username" label="">
                <el-input v-model="loginForm.username">
                </el-input>
            </el-form-item>
            <!--        -->
            <el-form-item prop="password" label="">
                <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item style="text-align: right">
                <el-button @click="resetLoginForm"></el-button>
                <el-button type="primary" @click="userLogin"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
import {mapState} from 'vuex'

export default {
    data() {
        return {
            user:{
                username:'',
                password:'',
                loginProvince:'',
                loginCity:'',
                loginLat:34.27, //
                loginLng:108.08, //
            },
            // 
            loginForm: {
                username: '',
                password: ''
            },
            // 
            loginFormRules: {
                // 
                username: [
                    // 
                    {required: true, message: "", trigger: "blur"},
                    {min: 2, max: 10, message: "2-10"}
                ],
                // 
                password: [
                    {required: true, message: "", trigger: "blur"},
                    {min: 6, max: 10, message: " 6  10 ", trigger: "blur"}
                ]
            },
        }
    },
    computed:{
       ...mapState([
           'loginFormVisiable'
       ])
    },
    methods: {
        resetLoginForm() {
            this.$store.commit('cancelLFV')
            this.$refs.loginFormRef.resetFields()
        },
        // 
        userLogin() {
            this.$refs.loginFormRef.validate(async valid => {
                if (!valid) return;
                this.user.username = this.loginForm.username
                this.user.password = this.$md5(this.loginForm.password)
                let local = await this.getLocalCity()
                this.user.loginProvince = local.result.ad_info.province
                this.user.loginCity = local.result.ad_info.city
                this.user.loginLat = local.result.location.lat
                this.user.loginLng = local.result.location.lng
                const {data: res} = await this.$blog.post('/login', {
                    user:this.user
                });
                if (res.code !== 200) return this.$message({message: '', type: 'error', offset: 80})
                this.$message({message: '', type: 'success', offset: 80});
                this.$refs.loginFormRef.resetFields()
                window.sessionStorage.setItem("token", JSON.stringify(res.data.token));
                window.sessionStorage.setItem("user", JSON.stringify(res.data.user));
                this.$store.commit('getUserInfo')
                this.$store.commit('cancelLFV')
            })
        },
        async getLocalCity(){
            let data = {
                key: 'IUTBZ-UHAKU-PD6VI-BZEEY-N3YT3-SCB6J',
                output:'jsonp'
            }
            let url = 'https://apis.map.qq.com/ws/location/v1/ip'
            // console.log(this.$jsonp(url, data))
            const res = await this.$jsonp(url, data)
            return res
        }
    }
}
</script>

<style lang="less" scoped>

    .login_form {
        width: 100%;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .btns {
        display: flex;
        justify-content: flex-end;
    }

    .login_dialog{
        opacity: 1;
    }

</style>
